.button--putih button {
  max-width: 25rem;
  width: 80vw;
  cursor: pointer;
  border: none;
  border-radius: 3.125rem;
  padding: 0.5rem 0;
  font-weight: 500;
  font-size: 1.125rem;
  background-color: var(--warna-putih);
  transition: all 0.5s ease;
}
@media screen and (min-width: 64rem) {
  .button--putih button {
    width: 8.125rem;
  }
}
.button--putih button:hover {
  color: var(--warna-hitam);
  background-color: var(--warna-abu-abu);
}
.button--hitam button {
  max-width: 25rem;
  width: 80vw;
  cursor: pointer;
  border: solid 0.0625rem black;
  border-radius: 3.125rem;
  padding: 0.5rem 0;
  font-weight: 500;
  font-size: 1.125rem;
  color: var(--warna-putih);
  background-color: black;
  transition: all 0.5s ease;
}
@media screen and (min-width: 64rem) {
  .button--hitam button {
    width: 8.125rem;
  }
}
.button--hitam button:hover {
  color: var(--warna-hitam);
  background-color: var(--warna-putih);
  border: solid 0.0625rem var(--warna-hitam);
}